import React from 'react';

import { BrowserRouter as Router, Route } from 'react-router-dom';

import Home  from './homePage';
import ListPage  from './Graphql';
import CreateGraphql  from './CreateGraphql';
import DetailPage  from './DetailGraphql';

import Header from '../Components/00-siteWide/header/header';
import Footer  from '../Components/00-siteWide/footer/footer';

import '../Assets/css/slick.min.css';
import '../Assets/css/styles.min.css';

export default () => (
  <Router>
    <div>
      <Header/>
      <Route exact path="/" component={Home}/>
      <Route exact path="/graphql" component={ListPage}/>
      <Route exact path="/graphql/create" component={CreateGraphql}/>
      <Route exact path="/graphql/detail/:id" component={DetailPage}/>
      <Footer/>
    </div>
  </Router>
);



