import React from 'react'
import { withRouter } from 'react-router-dom'
import { graphql} from 'react-apollo'
import gql from 'graphql-tag'

class CreatePage extends React.Component {
  state = {
    description: '',
    imageUrl: '',
    title: ''
  }

  render() {
    return (
      <div className='pa4 flex justify-center bg-white'>
        <div style={{maxWidth: 400}} className=''>
          {this.state.imageUrl &&
            <img
              src={this.state.imageUrl}
              alt=''
              className='w-100 mv3'
            />}
          <input
            className='w-100 pa3 mv2'
            value={this.state.imageUrl}
            placeholder='Image Url'
            onChange={e => this.setState({imageUrl: e.target.value})}
            autoFocus
          />
          <input
            className='w-100 pa3 mv2'
            value={this.state.description}
            placeholder='Description'
            onChange={e => this.setState({description: e.target.value})}
          />
          <input
            className='w-100 pa3 mv2'
            value={this.state.title}
            placeholder='Title'
            onChange={e => this.setState({title: e.target.value})}
          />
          {this.state.description &&
            this.state.imageUrl &&
            <button
              className='pa3 bg-black-10 bn dim ttu pointer'
              onClick={this.handleBanner}
            >
              Post
            </button>}
        </div>
      </div>
    )
  }

  handleBanner = async () => {
    const {description, imageUrl, title} = this.state
    await this.props.createBannerMutation({variables: {description, imageUrl, title}})
    this.props.history.replace('/graphql')
  }

}

const CREATE_POST_MUTATION = gql`
  mutation createBannerMutation($description: String!, $imageUrl: String!, $title: String!) {
    createBanner(description: $description, imageUrl: $imageUrl, title: $title) {
      id
      description
      imageUrl
      title
    }
  }
`

const CreatePageWithMutation = graphql(CREATE_POST_MUTATION, {name: 'createBannerMutation'})(CreatePage)
export default withRouter(CreatePageWithMutation)



